import React from "react";
import { Card, CardColumns } from "react-bootstrap";
import produto1 from "../img/produto1.jpg";
import produto4 from "../img/produto4.png";
import { Link } from "react-router-dom";

const Produtos = () => {
	return (
		<div class="container">
			<h2 className="plant">Produtos Naturais</h2>
			<p>Feitos artesanalmente, garantido sua saúde e beleza.</p>
			<p>Entrega feita em São Paulo, nas suas mãos.</p>
			<hr></hr>
			<div class="row">
				<div class="col-sm-6">
					<Card className="product">
						<Link to="/produtos/produto1">
							<Card.Img variant="top" className="roundimage" src={produto1} />
						</Link>
						<Card.Body>
							<Link className="glow2" to="/produtos/produto1">
								<Card.Title>Creme Noturno Revitalizante</Card.Title>
							</Link>
							<Card.Text>
								Criado para deixar as suas noites mais confortáveis.
							</Card.Text>
							<hr></hr>
							<p className="productprice">R$ 20,00</p>
						</Card.Body>
					</Card>
				</div>
				<div class="col-sm-6">
					<Card className="product">
						<Link to="/produtos/produto4">
							<Card.Img variant="top" className="roundimage" src={produto4} />
						</Link>
						<Card.Body>
							<Link className="glow2" to="/produtos/produto4">
								<Card.Title>Spray Facial Revigorante</Card.Title>
							</Link>
							<Card.Text>
								Refrescante e efetivo contra irritações e picadas. Spray
								perfeito para o verão!
							</Card.Text>
							<hr></hr>
							<p className="productprice">R$ 15,00</p>
						</Card.Body>
					</Card>
				</div>
			</div>
			<hr></hr>
		</div>
	);
};
export default Produtos;
